    flex-wrap: wrap;
    margin-top: 70px;
    img {
      width: 18%;
      border: 1px solid #bbb;
      margin: 10px;
      padding: 10px;
    }
  }
  .menu_icon {
    position: absolute;
    width: 30px;
    left: 50px;
    top: 55%;
  }
}
.item_img {
  width: 100%;
}

.swiper_container_wrap {
  overflow: hidden;
  position: relative;
  line-height: 10px;
  .menu_icon {
    position: absolute;
    width: 30px;
    left: 50px;
    top: 35%;
    z-index: 99
  }
}
// 
.footer {
  
  position: relative;
  width: 100%;
  .clound {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
  }
}
.spare{
  width:100%;
  height:30%;
  background-color:#ddd;
}
.btm {

  position: relative;
  top: 0;
  height: 70%;
  width: 70%;
  left: 15%;
  .logo_btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    /*top: 100px;*/
    top:10%;
    width: 130px;
  }
  .btm_wrap {
    /*margin-top: 90px;*/
    position: absolute;
    /*top: 160px;*/
    top:42%;
    width: 100%;
    @media screen {
      @media (min-width: 489px) {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
      }
    }

  }
  .btm_item {
    width: 33%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    position: relative;
    .qrcode {
      transform: scale(.92);
    }
    h4 {
      font-size: 30px;
    }
  }
  .btm_footer {
    position: absolute;
    width: 100%;
    bottom: 30px;
    .title {
      height: 40px;
      display: block;
      margin: 0 auto;
      z-index: 99;
    }
  }

}


.slideLeft-enter-active, .slideLeft-leave-active {
  transition: all 0.5s linear;
}
.slideLeft-enter, .slideLeft-leave-to {
  transform: translateX(-100%);
  // opacity: 0;
}
.side_menu {
  position: absolute;
  left: 20px;
  top: 50%;
  width: 30px;
  z-index: 77;
  img {
    width: 100%;
  }
}
.btm1{
  width:75%;
}
.btm2{
  width:55%;
  margin-top:12px;
}
.qrcode{
  width:32%;
  margin-top:2px;
}
.ver{
  width:0.1px;
  height:150px;
  border:1px solid #aaadaa;
}
.ttt{
  
  width: 100%;
  text-align: right;
}
</style>



